import React from 'react';
import Tilt from 'react-tilt';
import './Logo.css';

const Logo = ({onRouteChange, logo}) => {
	return (
		<div className='' style= {{ position: 'fixed', left: '0px', zIndex: '2000' }}>
			<Tilt className="Tilt" options={{ max : 45 }} style={{height: 150, width: 80 }} >
 				<div className="Tilt-inner">
					<img
						className = 'img-Border' 
						style={{marginTop: '60%', marginLeft: '10%'}}
						src={logo} 
						alt=''
						onClick={() => onRouteChange('home')} />
				</div>
			</Tilt>
		</div>
	);
}

export default Logo;




