import React, { Component } from 'react';
import Particles from 'react-particles-js';
import ImageSlider from '../components/ImageSlider/ImageSlider';
import Header from '../components/Header/Header';
import Links from '../components/Links/Links';
import Logo from '../components/Logo/Logo';
import SideNav from '../components/SideNav/SideNav';
import SiteContent from '../components/SiteContent/SiteContent';
import Calendar from '../components/Calendar/Calendar';
import Footer from '../components/Footer/Footer';
import city from '../components/Logo/TC_Mark_Black_RGB.png';
import './App.css';

/*Snowy background*/
const particlesOptions = {
  particles:{number:{value:100,density:{enable:true,value_area:800}},color:{value:"#fff"},shape:{type:'circle',stroke:{width:0,color:"#000000"},polygon:{nb_sides:5}},opacity:{value:0.5,random:true,anim:{enable:false,speed:1,opacity_min:0.1,sync:false}},size:{value:10,random:true,anim:{enable:false,speed:40,size_min:0.1,sync:false}},line_linked:{enable:false,distance:500,color:"#ffffff",opacity:0.4,width:2},move:{enable:true,speed:6,direction:'bottom',random:false,straight:false,out_mode:'out',bounce:false,attract:{enable:false,rotateX:600,rotateY:1200}}},interactivity:{detect_on:'canvas',events:{onhover:{enable:true,mode:'bubble'},onclick:{enable:true,mode:'repulse'},resize:true},modes:{grab:{distance:400,line_linked:{opacity:0.5}},bubble:{distance:400,size:4,duration:0.3,opacity:1,speed:3},repulse:{distance:200,duration:0.4},push:{particles_nb:4},remove:{particles_nb:2}}},retina_detect:true}

class App extends Component {
	constructor() {
		super();
		this.state = {
			logo: city,
			route: 'home',
			title: 'home'
		}
	}

/*To display page title when navigated between links */
	pageTitle = (route) => {
		let title;
		switch(route) {
			case 'q1': title = 'What is thriving?'; break;
			case 'q2' : title = 'Human Ecology'; break;
			case 'data' : title = 'Data'; break;
			case 'blog' : title = 'Blog'; break;
			case 'cities' : title = 'Cities'; break;
			case 'about' : title = ' About'; break;
			default: title = 'home'; break;
		}
		this.setState({title});
	}

/*Handle route changes from home to navigation links */
	onRouteChange = (route) => {
		if(route !== 'home' || route !== this.state.route) {
			this.setState({route});
		} else
			window.scrollTo({ 
  			top: 0,
  			left: 0, 
  			behavior: 'smooth' 
		});
		this.pageTitle(route);
	}

	render() {
    	const {route, title, logo} = this.state;
	    return (
	    	<div className= 'App'>
	    		<Logo logo={logo} onRouteChange={this.onRouteChange}/>
	    		{
	    			(route === 'home') ?  
	  					<div>
	  						<Particles className ='particles' params={particlesOptions} />
				    		<Header />
				    		<SideNav onRouteChange={this.onRouteChange}/>
				      		<ImageSlider />
							<SiteContent onRouteChange={this.onRouteChange}/>
							<Calendar />
							<Footer />
		      			</div>
		      		: <Links title = {title}/>
	    		}
	      </div>
	    );
	}
}

export default App;



