import React from 'react';

const SiteContent = ({onRouteChange}) => {
	return(
		<div>
			<h3 className='siteContent'>Thriving Cities is a group of unconventional urbanists with a unique understanding of, and approach to, cities.<br /><br />
				Thriving Cities is committed to helping scholars, foundations, city officials, religious leaders, politicians, business people, nonprofits, and residents alike answer the question, 
				<i>what does it mean and take to thrive in my city and how can I contribute?</i></h3>
			<button type='button' className='grow no-underline ba bw2 ph4 pv3 mb2 dib buttons foreground'>
				<a style={{textDecoration: 'none', color: 'white'}} href="http://explore.thrivingcities.com/">Our Newest Tool, The Indicator Explorer</a>
			</button>
		</div>
	)
}

export default SiteContent;


