import React from 'react';
import fb from './fb.png';
import twt from './twt.png';
import blog from './blog.png';

const Footer = () => {
	/*Page footer with fb, twitter, blog and subscribe feature in here */
	return (
		<div>
			<div className='flex justify-center foreground'>
				<a className='pa3 grow dib' href='https://www.facebook.com/thrivingcities'><img src={fb} alt='fb'/></a>
				<a className='pa3 grow dib' href='https://twitter.com/thriving_cities'><img src={twt} alt='fb' /></a>
				<a className='pa3 grow dib' href='http://thrivingcities.com/blog'><img src={blog} alt='fb' /></a>
			</div>
			<div className='pointer adobe-reg'>
				<form action="https://herokuapp.us17.list-manage.com/subscribe/post" method="POST">
				    <input type="hidden" name="u" value="b0f21ee6e197e7ea28e36cd64" />
				    <input type="hidden" name="id" value="d10dfec983" />
					<button className='no-underline ba bw2 ph4 pv3 mb2 dib buttons grow foreground'><input className='f4 ttu br3' style={{backgroundColor:'transparent', border: 'none', outline: 'none', marginTop:'0px'}} type="submit" value="Stay Connected!" /></button>
				</form>
			</div>
		</div>
	)
}

export default Footer;


