import React from 'react';
import Event from '../Event/Event';

const Calendar = () => {
	return (
		<div>
			<h1 className='ttu tc underline' 
				style={{
					fontFamily: 'Agency FB Bold', 
					fontSize: '60px', 
					textShadow: '2px 2px #FF0000'
				}}> Upcoming Events </h1>
			<Event />
		</div>
	)
}

export default Calendar;


