import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import menu from './menu.png';
import close from './close.png';
import './SideNav.css';
import classNames from 'classnames';

class SideNav extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showMenu : false,
			menuIcon : menu,
			onRouteChange: props.onRouteChange
		};
	}

	componentDidMount() {
		document.addEventListener('click', this.handleClick);
	}

	componentWillUnmount() {
		document.removeEventListener('click', this.handleClick);
	}

	handleClick = (event) => {
		let menuIcon = !this.state.showMenu ?  menu : close;
		this.setState({ menuIcon });
		const DOMNode = ReactDOM.findDOMNode(this);
		if(!DOMNode || !DOMNode.contains(event.target))
			this.setState({ showMenu : false, menuIcon : menu});
		let visibility = this.state.showMenu ? 'visible' : 'hidden';
		DOMNode.getElementsByTagName('ul')[0].style.visibility = visibility;
	} 

	toggleMenu = () => {
		this.setState({ showMenu : !this.state.showMenu });
	}

	render () {
		const { showMenu, menuIcon, onRouteChange } = this.state;
    	const sidebarClass = classNames({
	        'sidebar': true,
	        'sidebar-menu-expanded': showMenu,
	        'sidebar-menu-collapsed': !showMenu
	    });
		return (
			<nav style = {{ position : 'fixed', right: '0px', zIndex: '100'}} className = {sidebarClass}>
				<img className='menuIcon' src= {menuIcon} alt='menu' onClick={this.toggleMenu} />
				<ul style = {{ visibility : 'hidden'}}>
				  <li onClick={() => onRouteChange('q1')}>What is Thriving?</li>
				  <li onClick={() => onRouteChange('q2')}>Human Ecology</li>
				  <li onClick={() => onRouteChange('data')}>Data</li>
				  <li onClick={() => onRouteChange('cities')}>Cities</li>
				  <li onClick={() => onRouteChange('blog')}>Blog</li>
				  <li onClick={() => onRouteChange('about')}>About</li>
				</ul>

			</nav>
		);
	}
}

export default SideNav;


