import React from 'react';
import event from './event.jpg';

const Event = () => {
	return (
		<div className="flex justify-around flex-wrap foreground" style={{marginTop: '10%'}}>
			<div className='grow w-25 pa3 mr2'>
				<img className='img-Border' src={event} alt='event' width='100%' />
				<h2>Event Name</h2>
				<h4>Event Date</h4>
			</div>
			<div className='grow w-25 pa3 mr2'>
				<img className='img-Border' src={event} alt='event' width='100%' />
				<h2>Event Name</h2>
				<h4>Event Date</h4>
			</div>
			<div className='grow w-25 pa3 mr2'>
				<img className='img-Border' src={event} alt='event' width='100%' />
				<h2>Event Name</h2>
				<h4>Event Date</h4>
			</div>
			<div className='grow w-25 pa3 mr2'>
				<img className='img-Border' src={event} alt='event' width='100%' />
				<h2>Event Name</h2>
				<h4>Event Date</h4>
			</div>
			<div className='grow w-25 pa3 mr2'>
				<img className='img-Border' src={event} alt='event' width='100%' />
				<h2>Event Name</h2>
				<h4>Event Date</h4>
			</div>
			<div className='grow w-25 pa3 mr2'>
				<img className='img-Border' src={event} alt='event' width='100%' />
				<h2>Event Name</h2>
				<h4>Event Date</h4>
			</div>
		</div>
	)
}

export default Event;


